/* ****************************************************************************** */

/* ARTUR sida */

.apply_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.applyed_cources {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin-top: 0;
}

.apply_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 350px;
    margin-top: 25px;
    margin-left: 15%;
    background: #6AB98B;
    padding: 15px;
    width: 80%;
}

.apply_select {
    width: 90%;
    height: 65px;
    padding: 0 25px;
    margin-top: 35px;
    border-radius: 4px;
    border: #6AB98B 0.5px solid;
    color: #6AB98B;
    font-size: 16px;
    font-weight: 700;
    background: white;
}

.apply_label {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: #6AB98B;

}

.apply_input {
    display: flex;
    width: 90%;
    margin-top: 15px;
    padding: 15px 25px;
    font-size: 14px;
    font-weight: 300;
    border: #6AB98B 0.5px solid;
    background: white;
}

.apply_label_text {
    display: flex;
    padding: 5px 10px 5px 10px;
    color: #fff;
    background: #6AB98B;
}

.apply_textarea {
    display: flex;
    width: 90%;
    min-height: 100px;
    padding: 10px 25px;
    font-size: 14px;
    font-weight: 300;
    background: white;
    border: #6AB98B 0.5px solid;
}

.apply_btn {
    display: flex;
    margin-top: 15px;
    margin-bottom: 7px;
    border: none;
    background-color: #6AB98B;
    border: #fff 0.5px solid;
    padding: 10px 15px;
    border-radius: 4px;
    color: white;
}

.apply_btn:hover {
    background: none;
    border: #fff 0.5px solid;
    color: #fff;
    font-weight: bold;
}

.history_all {
    color: #646464;
}

.apply_course_history {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40%;
    padding: 0 0 20px 0;
}

.history_li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.history_title {
    font-size: 24px;
    color: #6AB98B;
    padding: 15px 0 15px 0;
}

.history_username {
    color: #646464;
    padding: 10px 0;
}

.history_email {
    color: #646464;
    font-size: 14px;
    padding: 10px 0;
}

.history_message {
    color: #646464;
    padding: 10px;
    font-size: 14px;
}


 /* ARTUR */